import React, { Component } from "react";
import Login from "../../components/UserManagement/Login";
import Register from "../../components/UserManagement/Register";
import "./LandingStyle.css";
import { createNewUser } from "../../actions/securityActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";

class Landing extends Component {
  constructor(props) {
    super(props);
    this.state = {
      errors: {},
      icon: <i class="fas fa-sign-in-alt mr-2"></i>,
      isLoginActive: true
    };
  }

  componentDidMount() {
    if(this.props.security.validToken){
      this.props.history.push("/dashboard");
    }
    this.rightSide.classList.add("right");
  }

  changeState() {
    const { isLoginActive } = this.state;
    if (isLoginActive) {
      this.rightSide.classList.remove("right");
      this.rightSide.classList.add("left");
    } else {
      this.rightSide.classList.remove("left");
      this.rightSide.classList.add("right");
    }

    this.setState(prevState => ({ isLoginActive: !prevState.isLoginActive }));
  }

  
  render() {
    const { isLoginActive } = this.state;
    const current = isLoginActive ? "Register" : "Login";
    const currentActive = isLoginActive ? "Login" : "Register";
    return (
      <div className="landing">
        <div className="light-overlay landing-inner text-dark">
          <div className="container">
            <div className="row">
              <div className="col-md-12 text-center">
                <h1 className="display-3 mb-4">Project Manager</h1>
                <p className="lead">
                  Let the learning journal begin
                </p>
                <br/>
                <div className="login">
                  <div
                    className="landing-container"
                    ref={ref => (this.containerRef = ref)}
                  >
                    {isLoginActive && (
                      <Login
                        history={this.props.history}
                        containerRef={ref => (this.current = ref)}
                      />
                    )}
                    {!isLoginActive && (
                      <Register
                        containerRef={ref => (this.current = ref)}
                      />
                    )}
                    <br />
                  </div>
                  <LeftSide
                    current={current}
                    icon = {this.state.icon}
                    containerRef={ref => (this.rightSide = ref)}
                    currentActive={{ currentActive }}
                    onClick={this.changeState.bind(this)}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <br/>
        <br/>
      </div>
    );
  }
}
// const getIcon = () =>{
//   if(this.state.isLoginActive){
//     return <i class="fas fa-sign-in-alt mr-2"></i>
//   }
//   else{
//     return <i class="fas fa-user-plus mr-2"></i>
//   }
// }

const LeftSide = props => {
  const loginIcon = props.current === "Login" ? <i className="fas fa-sign-in-alt mr-2"></i> : "";
  const registerIcon = loginIcon === "" ? <i className="fas fa-user-plus ml-2"></i> : "";
  return (
    <div
      className="right-side"
      ref={props.containerRef}
      onClick={props.onClick}
    >
      <div className="inner-container">
        <div className="text">{loginIcon}{props.current}{registerIcon}</div>
      </div>
    </div>
  );
};

Landing.propTypes = {
  createNewUser: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired,
  security: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  errors: state.errors,
  security: state.security
})

export default connect(mapStateToProps, {createNewUser}) (Landing);
