import React, { Component } from "react";
import { Link } from "react-router-dom";
import Backlog from "./Backlog";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { getBacklog } from "../../actions/backlogActions";

class ProjectBoard extends Component {
  // construtor to handle errors
  constructor() {
    super();
    this.state = {
      errors: {}
    };
  }

  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.getBacklog(id);
  }

  static getDerivedStateFromProps(props, state){
      if(props.errors){
          return{
              errors: props.errors
          }
      }
  }

  render() {
    const { id } = this.props.match.params;
    const { project_tasks } = this.props.backlog;
    const {errors} = this.state;

    //console.log(this.props);

    let boardContent;

    const boardAlgorithm = (errors, project_tasks) => {
      //console.log(project_tasks)
        if(project_tasks.length === 0){
            if(errors === null){
                return(
                    <div className="alert alert-danger text-center" role="alert">
                        {errors.message}
                    </div>
                );
            }
            else{
                return(
                    <div className="alert alert-info text-center" role="alert" >
                        No Project Tasks on this Project
                    </div>
                )
            }
        }
        else{
            return <Backlog project_tasks_prop={project_tasks} />;
        }
    };

    boardContent = boardAlgorithm(errors, project_tasks);

    return (
      <div className="container">
      <h1 className="display-4 text-center mb-4"><i className="fas fa-tasks mr-3"></i>Project Tasks for {id}</h1>
        <Link to={`/addProjectTask/${id}`} className="btn btn-primary">
          <i className="fas fa-plus-circle"> Create Project Task</i>
        </Link>
        <br />
        <hr />
        {boardContent}
      </div>
    );
  }
}

ProjectBoard.propTypes = {
  backlog: PropTypes.object.isRequired,
  getBacklog: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  backlog: state.backlog,
  errors: state.errors
});
export default connect(mapStateToProps, { getBacklog })(ProjectBoard);
