import React, { Component } from "react";
import { getProject, createProject } from "../../actions/projectActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import classnames from "classnames";

class UpdateProject extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: "",
      projectName: "",
      projectIdentifier: "",
      description: "",
      start_date: "",
      end_date: "",
      errors: {}
    };
  }

  // componentWillReceiveProps(nextPros){
  //   if(nextPros.errors){
  //     this.setState({errors: nextPros.errors});
  //   }
  //   const {
  //     id,
  //     projectName,
  //     projectIdentifier,
  //     description,
  //     start_date,
  //     end_date,
  //   } = nextPros.project;

  //   this.setState({
  //     id,
  //     projectName,
  //     projectIdentifier,
  //     description,
  //     start_date,
  //     end_date,
  //   });
  // }

  static getDerivedStateFromProps(props, state) {
    //console.log(state);
    if (props.errors) {
      return { errors: props.errors };
    } else {
      return null;
    }
  }

  async componentDidMount() {
    //console.log(this.props);
    await this.props.getProject(this.props.match.params.id, this.props.history);
    const {
      id,
      projectName,
      projectIdentifier,
      description,
      start_date,
      end_date
    } = this.props.project;
    this.setState({
      id,
      projectName,
      projectIdentifier,
      description,
      start_date,
      end_date
    });
  }

  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  onSubmit = e => {
    e.preventDefault();
    const updatedProject = {
      id: this.state.id,
      projectName: this.state.projectName,
      projectIdentifier: this.state.projectIdentifier,
      description: this.state.description,
      start_date: this.state.start_date,
      end_date: this.state.end_date
    };
    //console.log(updatedProject);
    this.props.createProject(updatedProject, this.props.history);
  };
  render() {
    const { errors } = this.state;
    //console.log(this.state);
    return (
      <div className="register">
        <div className="container">
          <div className="row">
            <div className="col-md-8 m-auto">
              <h2 className="display-4 text-center">Update Project form</h2>
              <hr />
              <form onSubmit={this.onSubmit}>
                <h4>Project Name</h4>
                <div className="form-group">
                  <input
                    type="text"
                    className={classnames("form-control form-control-lg", {
                      "is-invalid": errors.projectName
                    })}
                    placeholder="Project Name"
                    name="projectName"
                    value={this.state.projectName || ""}
                    onChange={this.onChange}
                  />
                  {errors.projectName && (
                    <div className="invalid-feedback">{errors.projectName}</div>
                  )}
                </div>
                <h4>Project Identifier</h4>
                <div className="form-group">
                  <input
                    type="text"
                    className="form-control form-control-lg"
                    placeholder="Unique Project ID"
                    name="projectIdentifier"
                    value={this.state.projectIdentifier || ""}
                    disabled
                  />
                </div>

                <h4>Project Description</h4>
                <div className="form-group">
                  <textarea
                    className={classnames("form-control form-control-lg", {
                      "is-invalid": errors.description
                    })}
                    placeholder="Project Description"
                    name="description"
                    value={this.state.description || ""}
                    onChange={this.onChange}
                  ></textarea>
                  {errors.description && (
                    <div className="invalid-feedback">{errors.description}</div>
                  )}
                </div>
                <h4>Start Date</h4>
                <div className="form-group">
                  <input
                    type="date"
                    className="form-control form-control-lg"
                    name="start_date"
                    value={this.state.start_date || ""}
                    onChange={this.onChange}
                  />
                </div>
                <h4>Estimated End Date</h4>
                <div className="form-group">
                  <input
                    type="date"
                    className="form-control form-control-lg"
                    name="end_date"
                    value={this.state.end_date || ""}
                    onChange={this.onChange}
                  />
                </div>

                <input
                  type="submit"
                  className="btn btn-primary btn-block mt-4"
                />
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

UpdateProject.propTypes = {
  getProject: PropTypes.func.isRequired,
  createProject: PropTypes.func.isRequired,
  project: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  project: state.project.project,
  errors: state.errors
});

export default connect(mapStateToProps, { getProject, createProject })(
  UpdateProject
);
