import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { deleteProject } from "../../actions/projectActions";

class ProjectItem extends Component {
  onDeleteClick = id => {
    this.props.deleteProject(id);
  };

  render() {
    const { project } = this.props;
    return (
      <div className="container" style={{ width: 506 }}>
        <div className="card card-body bg-light mb-3">
          <div className="col-20">
            <span className="mx-auto mb-2">{`ID: ${project.projectIdentifier}`}</span>
          </div>
          <div className="col-lg-20 col-md-20 col-20">
            <h3>{`Nameï¼š${project.projectName}`}</h3>
            <p>{`Description: ${project.description}`}</p>
          </div>
          <div className="col-md-40 d-lg-block">
            <div className="d-flex flex-wrap">
              <Link to={`/projectBoard/${project.projectIdentifier}`}>
                <div className="card card-body bg-light board">
                  <i className="fa fa-flag-checkered pr-1 text-center">
                    <br />
                    Project Board{" "}
                  </i>
                </div>
              </Link>
              <Link to={`/updateProject/${project.projectIdentifier}`}>
                <div className="card card-body bg-light update">
                  <i className="fa fa-edit pr-1 text-center">
                    <br />
                    Update Project
                  </i>
                </div>
              </Link>
              <div
                className="card card-body bg-light delete"
                onClick={() => this.onDeleteClick(project.projectIdentifier)}
              >
                <i className="fa fa-minus-circle pr-1 text-center">
                  <br />
                  Delete Project
                </i>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

ProjectItem.propTypes = {
  deleteProject: PropTypes.func.isRequired
};

export default connect(null, { deleteProject })(ProjectItem);
