import React, { Component } from "react";
import { connect } from "react-redux";
import classnames from "classnames";
import {
  getProjectTask,
  updateProjectTask
} from "../../../actions/backlogActions";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";

class UpdateProjectTask extends Component {
  constructor() {
    super();

    this.state = {
      id: "",
      projectSequence: "",
      summary: "",
      acceptanceCriteria: "",
      status: "",
      priority: "",
      dueDate: "",
      projectIdentifier: "",
      create_At: ""
    };
  }
  componentDidMount() {
    //   // if theres is error
    //   if(Object.entries(this.props.errors).length !== 0 || this.props.errors.constructor !== Object){
    //       this.props.history.push("/dashboard");
    //       return;
    //   }
    // console.log(Object.entries(this.props.errors).length !== 0);
    // console.log(this.props.errors.constructor !== Object);

    const { backlog_id, pt_id } = this.props.match.params;
    this.props
      .getProjectTask(backlog_id, pt_id, this.props.history)
      .then(() => {
                // if theres is error
        if(Object.entries(this.props.errors).length !== 0 || this.props.errors.constructor !== Object){
            return;
        }
        const {
          id,
          projectSequence,
          summary,
          acceptanceCriteria,
          status,
          priority,
          dueDate,
          projectIdentifier,
          create_At
        } = this.props.project_task;
        this.setState({
          id,
          projectSequence,
          summary,
          acceptanceCriteria,
          status,
          priority,
          dueDate,
          projectIdentifier,
          create_At
        });
       });
  }

  //   componentWillReceiveProps(nextProps) {
  //       console.log(nextProps);
  //     const {
  //       id,
  //       projectSequence,
  //       summary,
  //       acceptanceCriteria,
  //       status,
  //       priority,
  //       dueDate,
  //       projectIdentifier,
  //       create_At
  //     } = nextProps.project_task;

  //     this.setState({
  //       id,
  //       projectSequence,
  //       summary,
  //       acceptanceCriteria,
  //       status,
  //       priority,
  //       dueDate,
  //       projectIdentifier,
  //       create_At
  //     });
  //   }

  //   static getDerivedStateFromProps(props, state) {
  //     console.log(props);
  //     if (props.project_task !== undefined) {
  //       const {
  //         id,
  //         projectSequence,
  //         summary,
  //         acceptanceCriteria,
  //         status,
  //         priority,
  //         dueDate,
  //         projectIdentifier,
  //         create_At
  //       } = props.project_task;

  //       const newId = id === state.id ? state.id : id;

  //       return {
  //         id: id,
  //         projectSequence: projectSequence,
  //         summary: summary,
  //         acceptanceCriteria: acceptanceCriteria,
  //         status: status,
  //         priority: priority,
  //         dueDate: dueDate,
  //         projectIdentifier: projectIdentifier,
  //         create_At: create_At
  //       };
  //     }
  //   }
  static getDerivedStateFromProps(props, state) {
      if(props.errors !== state.errors){
          return {
              errors: props.errors,
          }
      }
      else{
        return null;
      }
  }

  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  onSubmit = e => {
    e.preventDefault();

    const updatedProjectTask = {
      id: this.state.id,
      projectSequence: this.state.projectSequence,
      summary: this.state.summary,
      acceptanceCriteria: this.state.acceptanceCriteria,
      status: this.state.status,
      priority: this.state.priority,
      dueDate: this.state.dueDate,
      projectIdentifier: this.state.projectIdentifier,
      create_At: this.state.create_At,
      errors: {}
    };

    //console.log(updatedProjectTask);
    this.props.updateProjectTask(
      this.state.projectIdentifier,
      this.state.projectSequence,
      this.props.history,
      updatedProjectTask
    );
  };

  render() {
      const {errors} = this.state;
    //     // if theres is error
    //   if(Object.entries(errors).length !== 0 || errors.constructor !== Object){
    //       this.props.history.push("/dashboard");
    //       return;
    //   }
    //console.log(this.state);

    return (
      <div className="add-PBI">
      <Link
      to={`/projectBoard/${this.state.projectIdentifier}`}
      className="btn btn-dark ml-5"
    >
    <i className="fas fa-hand-point-left mr-2"></i>
      Back to Project Board
    </Link>
        <div className="container">
          <div className="row">
            <div className="col-md-8 m-auto">
              <h2 className="display-4 text-center mb-4">
                Add /Update Project Task
              </h2>

              <h4>Task Name</h4>
              <form onSubmit={this.onSubmit}>
                <div className="form-group">
                  <input
                    type="text"
                    className={classnames("form-control form-control-lg", {
                        "is-invalid": errors.summary
                    })}
                    name="summary"
                    placeholder="Project Task summary"
                    value={this.state.summary}
                    onChange={this.onChange}
                  />
                  {
                      errors.summary && (
                          <div className="invalid-feedback">{errors.summary}</div>
                      )
                  }
                </div>
                <h4>Task Description</h4>
                <div className="form-group">
                  <textarea
                    className="form-control form-control-lg"
                    placeholder="Acceptance Criteria"
                    name="acceptanceCriteria"
                    value={this.state.acceptanceCriteria}
                    onChange={this.onChange}
                  ></textarea>
                </div>
                <h4>Due Date</h4>
                <div className="form-group">
                  <input
                    type="date"
                    className="form-control form-control-lg"
                    name="dueDate"
                    value={this.state.dueDate}
                    onChange={this.onChange}
                  />
                </div>
                <h4>Priority</h4>
                <div className="form-group">
                  <select
                    className="form-control form-control-lg"
                    name="priority"
                    value={this.state.priority}
                    onChange={this.onChange}
                  >
                    <option value={0}>Select Priority</option>
                    <option value={1}>High</option>
                    <option value={2}>Medium</option>
                    <option value={3}>Low</option>
                  </select>
                </div>

                <h4>Status</h4>
                <div className="form-group">
                  <select
                    className="form-control form-control-lg"
                    name="status"
                    value={this.state.status}
                    onChange={this.onChange}
                  >
                    <option value="">Select Status</option>
                    <option value="TO_DO">TO DO</option>
                    <option value="IN_PROGRESS">IN PROGRESS</option>
                    <option value="DONE">DONE</option>
                  </select>
                </div>

                <input
                  type="submit"
                  className="btn btn-primary btn-block mt-4"
                />
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

UpdateProjectTask.propTypes = {
  getProjectTask: PropTypes.func.isRequired,
  project_task: PropTypes.object.isRequired,
  updateProjectTask: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  project_task: state.backlog.project_task,
  errors: state.errors
});

export default connect(mapStateToProps, { getProjectTask, updateProjectTask })(
  UpdateProjectTask
);
