import React, { Component } from "react";
import ProjectItem from "./Project/ProjectItem";
import CreateProjectButton from "./Project/CreateProjectButton";
import { connect } from "react-redux";
import { getProjects } from "../actions/projectActions";
import PropTypes from "prop-types";

class Dashboard extends Component {

  componentDidMount() {
    this.props.getProjects();
  }

  render() {
    const projects = this.props.project.projects;
    //console.log(projects.length);
    var projectNum = projects.length;
    const RenderProjects = () => {
      if (projects.length === 0) {
        return (
          <div className="alert alert-primary text-center" role="alert">
            You do not have any project. Click "Create Project" to create your
            first project
          </div>
        );
      } else {
        let projectsArr = [];
        let count = 0;
        while (projectNum > 0) {
          if (projectNum > 1) {
            projectsArr.push(
              <div className="row" key={count}>
                <ProjectItem className="float-left" project={projects[count++]} />
                <ProjectItem className="float-right" project={projects[count++]} />
              </div>
            );
            projectNum -= 2;
          } else {
            projectsArr.push(<ProjectItem key={count} project={projects[count++]} />);
            projectNum--;
          }

        }
        //console.log(projectsArr);
        return projectsArr;
      }
    };
    return (
      <div className="projects">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <h1 className="display-4 text-center">Projects Dashboard</h1>
              <br />
              <CreateProjectButton/>

              <br />
              <hr />
              <RenderProjects/>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Dashboard.propTypes = {
  project: PropTypes.object.isRequired,
  getProjects: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  project: state.project
});

export default connect(mapStateToProps, { getProjects })(Dashboard);
