import React, { Component } from "react";

import { createNewUser } from "../../actions/securityActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import classnames from "classnames";

class Register extends Component {
  constructor() {
    super();

    this.state = {
      username: "",
      fullName: "",
      password: "",
      confirmPassword: "",
      message: "",
      errors: {}
    };
  }

  static getDerivedStateFromProps(props, state) {
    if (JSON.stringify(props.errors) !== JSON.stringify(state.errors)) {
      return {
        errors: props.errors
      };
    } else {
      return null;
    }
  }
  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  onSubmit = e => {
    e.preventDefault();
    this.setState({ message: "" });
    //console.log(this.state);

    const newUser = {
      username: this.state.username,
      fullName: this.state.fullName,
      password: this.state.password,
      confirmPassword: this.state.confirmPassword
    };
    //console.log(this.props);
    this.props.createNewUser(newUser).then(() => {
     //console.log(Object.entries(this.props.errors).length);
      if (Object.entries(this.props.errors).length === 0) {
        this.setState({ message: "Succuss! You can log in now!" });
      }
    });
  };

  render() {
    const {errors} = this.state;
    return (
      <div className="register">
        <div className="container">
          <div className="row">
            <div className="m-auto">
              <h1 className="display-4 text-center">Sign Up</h1>
              <br/>
              <form onSubmit={this.onSubmit}>
                <div className="form-group">
                  <input
                    type="text"
                    className={classnames("form-control form-control-lg", {
                      "is-invalid": errors.fullName
                    })}
                    placeholder="Full Name"
                    name="fullName"
                    value={this.state.fullname}
                    onChange={this.onChange}
                  />
                  {
                    errors.fullName && (
                      <div className="invalid-feedback">{errors.fullName}</div>
                    )
                  }
                </div>
                <div className="form-group">
                  <input
                    type="text"
                    className={classnames("form-control form-control-lg", {
                      "is-invalid": errors.username
                    })}
                    placeholder="Email (Username)"
                    name="username"
                    value={this.state.username}
                    onChange={this.onChange}
                  />
                  {
                    errors.username && (
                      <div className="invalid-feedback">{errors.username}</div>
                    )
                  }
                </div>
                <div className="form-group">
                  <input
                    type="password"
                    className={classnames("form-control form-control-lg", {
                      "is-invalid": errors.password
                    })}
                    placeholder="Password"
                    name="password"
                    value={this.state.password}
                    onChange={this.onChange}
                  />
                  {
                    errors.password && (
                      <div className="invalid-feedback">{errors.password}</div>
                    )
                  }
                </div>
                <div className="form-group">
                  <input
                    type="password"
                    className={classnames("form-control form-control-lg", {
                      "is-invalid": errors.confirmPassword
                    })}
                    placeholder="Confirm Password"
                    name="confirmPassword"
                    value={this.state.confirmPassword}
                    onChange={this.onChange}
                  />
                  {
                    errors.confirmPassword && (
                      <div className="invalid-feedback">{errors.confirmPassword}</div>
                    )
                  }
                </div>
                <p style={{ color: "blue" }}>{this.state.message}</p>
                <input
                  type="submit"
                  className="btn btn-success btn-block mt-4"
                />
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Register.propTypes = {
  createNewUser: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired,
  security: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  errors: state.errors,
  security: state.security
});
export default connect(mapStateToProps, { createNewUser })(Register);
